<template>
    <div class="login" >
      <div class="mainLogin" :style="oHeight">
        <img src="../../assets/img/title2.png" alt="">
        <Input v-model="userLogin.telNumber" icon="person" placeholder="" style="width: 200px"></Input>
        <Input v-model="userLogin.pwd" icon="locked" type="password" placeholder="" style="width: 200px"></Input>
        <div class="check">
          <Checkbox v-model="remember" class="checkout"></Checkbox>
          <span></span>
        </div>
        
        <Button class="login-btn" style="width: 200px" @click="loginUser"></Button>
        
        <span class="new-user" @click="clickRegister"></span>
      </div>
      <Modal class="registerModal" v-model="showModal" width="360">
        <p slot="header" style="color:#1cbe99; text-align:center">
          <Icon type="edit"></Icon>
          <span></span>
        </p>
        <div class="registerModal" style="text-align:center">
          <span class="text"></span>
          <Input v-model="userInfo.telNumber" placeholder="" clearable style="width: 200px" @on-blur="checkTel"></Input>
          <br><br>
          <span class="text"></span>
          <Input v-model="userInfo.userName" placeholder="" clearable style="width: 200px" @on-blur="checkName"></Input>
          <br><br>
          <span class="text"></span>
          <Input v-model="userInfo.pwd" type="password" placeholder="" clearable style="width: 200px" @on-blur="checkPwd"></Input>
          <br><br>
          <span class="text"></span>
          <Input v-model="confirmPwd" type="password" placeholder="" clearable style="width: 200px" @on-blur="checkConfirmPwd"></Input>
          <br><br>
          <span class="text"></span>
          <Input v-model="userInfo.realName" placeholder="" clearable style="width: 200px"></Input>
          <br><br>
          <span class="text"></span>
          <Input v-model="userInfo.idCard" placeholder="" clearable style="width: 200px" @on-blur="checkCard"></Input>
          <br>
        </div>
        <div slot="footer">
          <Button class="register-btn" size="large" long @click.prevent="submitRegister"></Button>
        </div>
      </Modal>
    </div>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import { Input, Button, Message, Modal, Icon, Checkbox } from "iview";
import router from "../../router";
import store from "../../store/store";
import { mapState, mapMutations } from "vuex";

axios.defaults.withCredentials = true;

Vue.prototype.$Message = Message;
export default {
  data() {
    return {
      userLogin: {
        telNumber: "",
        pwd: ""
      },
      userInfo: {
        userName: "",
        pwd: "",
        telNumber: "",
        realName: "",
        idCard: ""
      },
      regUser: {
        // 
        telNumber: /(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}/,
        //  
        userName: /[\u4E00-\u9FA5A-Za-z0-9]+$/,
        //  6-16
        pwd: /\w{6,16}/,
        // 
        idCard: /(?:1[1-5]|2[1-3]|3[1-7]|4[1-6]|5[0-4]|6[1-5])\d{4}(?:1[89]|20)\d{2}(?:0[1-9]|1[0-2])(?:0[1-9]|[12]\d|3[01])\d{3}(?:\d|[xX])/
      },
      confirmPwd: "",
      showModal: false,
      remember: false,
      oHeight: {
        height: window.screen.height + "px"
      }
    };
  },
  store,
  components: {
    Input,
    Button,
    Modal,
    Icon,
    Checkbox
  },
  created() {
    // localStrogeuserLogin
    let userLogin = {
      telNumber: window.localStorage.getItem("telNumber"),
      pwd: window.localStorage.getItem("pwd")
    };
    // 
    if (userLogin.telNumber && userLogin.pwd) {
      this._loginPost(userLogin);
    }
  },
  computed: {
    ...mapState(["userId", "pwd","userName"]),
  },
  methods: {
    ...mapMutations(["update"]),
    _loginPost(userLogin) {
      // localStroge,
      if (!window.localStorage.getItem("telNumber")) {
        window.localStorage.setItem("telNumber", this.userLogin.telNumber);
        window.localStorage.setItem("pwd", this.userLogin.pwd);
      }
      axios
        .post("http://120.78.86.45/auth/login", {
          userLogin: userLogin
        })
        .then(res => {
          if (res.data.isLogin == true) {
            this.$Message.success(res.data.message);
            this.update({
              userId: res.data.userId,
              pwd: res.data.pwd,
              userName: res.data.userName,
            })
            this.$router.push("footer/tomato");
          } else {
            this.$Message.error(res.data.message);
          }
        })
        .catch();
    },
    loginUser() {
      if (this.userLogin.telNumber == "" || this.userLogin.pwd == "") {
        this.$Message.warning("");
      } else {
        this._loginPost(this.userLogin);
      }
    },
    // 
    checkTel() {
      if (
        this.userInfo.telNumber != "" &&
        !this.regUser.telNumber.test(this.userInfo.telNumber)
      ) {
        this.$Message.warning("");
      } else {
        return true;
      }
    },
    // 
    checkName() {
      if (
        this.userInfo.userName != "" &&
        !this.regUser.userName.test(this.userInfo.userName)
      ) {
        this.$Message.warning("");
      } else {
        return true;
      }
    },
    // 
    checkPwd() {
      if (
        this.userInfo.pwd != "" &&
        !this.regUser.pwd.test(this.userInfo.pwd)
      ) {
        this.$Message.warning("");
      } else {
        return true;
      }
    },
    // confirmPwd
    checkConfirmPwd() {
      if (this.confirmPwd != "" && this.confirmPwd != this.userInfo.pwd) {
        this.$Message.warning("");
      } else {
        return true;
      }
    },
    // 
    checkCard() {
      if (
        this.userInfo.idCard != "" &&
        !this.regUser.idCard.test(this.userInfo.idCard)
      ) {
        this.$Message.warning("");
      } else {
        return true;
      }
    },
    // Modal
    clickRegister() {
      this.showModal = true;
    },
    // 
    submitRegister() {
      if (
        this.checkTel() &&
        this.checkName() &&
        this.checkPwd() &&
        this.checkConfirmPwd() &&
        this.checkCard()
      ) {
        axios
          .post("http://120.78.86.45/auth/register", {
            userInfo: this.userInfo
          })
          .then(res => {
            if (res.data.isRegister == true) {
              this.$Message.success(res.data.message);
              this.$router.push("/footer/tomato");
            } else {
              this.$Message.error(res.data.message);
            }
          })
          .catch();
      }
    }
  }
};
</script>

<style>
.mainLogin {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #1cbe99;
}
/* img */
.mainLogin img {
  padding: 100px 0 30px;
}
/* inputicon */
.mainLogin .ivu-input {
  padding: 4px 32px;
}
.mainLogin .ivu-input-icon {
  left: 0;
}
.mainLogin .ivu-input-wrapper {
  background: hsla(0, 0%, 100%, 0.3);
  margin: 20px 0 0;
}
/*  */
.mainLogin .check {
  display: flex;
  margin: 10px 0;
  padding-left: 2px;
  width: 200px;
  color: #fff;
  justify-content: space-between;
}
.mainLogin .check .ivu-checkbox-checked .ivu-checkbox-inner {
  border-color: #ffffff6c;
  background-color: #1cbe99;
}
.mainLogin .check .ivu-checkbox-focus {
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.486);
}
.mainLogin .check span {
  margin: 0;
}
/*  */
.mainLogin .login-btn {
  color: #fff;
  border-color: rgba(24, 111, 98, 0.219);
  background-color: #ff9900;
}
/*  */
.mainLogin .new-user {
  display: flex;
  margin-top: 5px;
  width: 200px;
  color: #fff;
  justify-content: flex-end;
}
/* Modal */
.registerModal .ivu-modal-content {
  border-radius: 0;
  background-color: #ffffffe3;
}
.registerModal .ivu-input-focus :hover {
  box-shadow: 0 0 0 2px rgba(28, 190, 153, 0.2);
}
.registerModal .ivu-input:hover {
  border-color: #1cbe99;
}
.registerModal .text {
  display: inline-block;
  width: 70px;
  text-align: right;
}
.registerModal .register-btn {
  color: #fff;
  border-color: rgba(24, 111, 98, 0.219);
  background-color: #1cbe99;
}
</style>



